import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Params} from '@angular/router';
@Component({
  selector: 'app-common',
  templateUrl: './common.component.html',
  styleUrls: ['./common.component.css']
})
export class CommonComponent implements OnInit {
  private account: string;
  private nickname: string;
  private rank: string;

  constructor(private routerIonfo: ActivatedRoute) {

     routerIonfo.params.subscribe((params: Params) => this.account = params['account']);
     routerIonfo.params.subscribe((params: Params) => this.nickname = params['nickname']);
     routerIonfo.params.subscribe((params: Params) => this.rank = params['rank']);
   }

  ngOnInit() {
  }

}
export class Product {
    constructor(
      public account: string,
      public nickname: string,
      public rank: string
    ){}
  }



