import { Component, OnInit } from '@angular/core';
import  * as $  from  'jquery';  //引入jquery

@Component({
  selector: 'app-binding-role',
  templateUrl: './binding-role.component.html',
  styleUrls: ['./binding-role.component.css']
})
export class BindingRoleComponent implements OnInit {

  constructor() { }

  ngOnInit() {

    function  click(){  //点击加背景
      $(".textarea li").click(function(){
        $(".textarea li").removeClass("roleActive");
        $(this).addClass("roleActive");
      });
    }
    click();


    $('.role02').css("margin-top",($('.role01').height()-$('.role02').height())/2+$(".role01 p").height()/2);


  }



}



