import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UserManagementComponent } from './user-management/user-management.component';
import { MenuManagementComponent } from './menu-management/menu-management.component';
import { PasswordComponent } from './password/password.component';
import { LogRecordComponent } from './log-record/log-record.component';
import { ResourcesManagementComponent } from './resources-management/resources-management.component';
import { RoleManagementComponent } from './role-management/role-management.component';
import { VipManagementComponent } from './vip-management/vip-management.component';
import { ProductMenuManagementComponent } from './product-menu-management/product-menu-management.component';
import { ProductResourceManagementComponent } from './product-resource-management/product-resource-management.component';
import { ProductRoleManagementComponent } from './product-role-management/product-role-management.component';
import { ProductConfigurationComponent } from './product-configuration/product-configuration.component';
import { CommodityManagementComponent } from './commodity-management/commodity-management.component';
import { AdduserComponent } from './adduser/adduser.component';
import { UserModifyPasswordComponent } from './user-modify-password/user-modify-password.component';
import { AddmenuComponent } from './addmenu/addmenu.component';
import { AddresourcesComponent } from './addresources/addresources.component';
import { ExtensionAgentComponent } from './extension-agent/extension-agent.component';
import { DealQueryComponent } from './deal-query/deal-query.component';
import { OrderReviewComponent } from './order-review/order-review.component';
import { CreateOrderComponent } from './create-order/create-order.component';
import { DemographicComponent } from './demographic/demographic.component';
import { TradeStatisticComponent } from './trade-statistic/trade-statistic.component';
import { ExtensionAddComponent } from './extension-add/extension-add.component';

import {BindingRoleComponent} from './binding-role/binding-role.component';
import {AddroleComponent} from './addrole/addrole.component';
import {BindingResourcesComponent} from './binding-resources/binding-resources.component';
import {AddVipComponent} from './add-vip/add-vip.component';
import {VipModifyPasswordComponent} from './vip-modify-password/vip-modify-password.component';
import {VipOpenComponent} from './vip-open/vip-open.component';
import {AddProductMenuComponent} from './add-product-menu/add-product-menu.component';



import { CommoditySalesManagementComponent } from './commodity-sales-management/commodity-sales-management.component';
import { SalesManagementAddComponent } from './sales-management-add/sales-management-add.component';
import {ProductConfigurationAddComponent} from './product-configuration-add/product-configuration-add.component';
import {ProductRoleManagementAddComponent} from './product-role-management-add/product-role-management-add.component';
import {ProductRoleManagemenBindingComponent} from './product-role-managemen-binding/product-role-managemen-binding.component';
import {ProductResourceManagementAddComponent} from './product-resource-management-add/product-resource-management-add.component';
import {CommodityManagementAddComponent} from './commodity-management-add/commodity-management-add.component';
import {ProductResourceManagementBindingComponent} from './product-resource-management-binding/product-resource-management-binding.component';

const routeConfig: Routes = [
  {path: '', redirectTo: '/user-management', pathMatch: 'full'},
  {path: 'user-management/:account/:nickname/:rank', component: UserManagementComponent},
  {path: 'menu-management/:account/:nickname/:rank', component: MenuManagementComponent},
  {path: 'password', component: PasswordComponent},
  {path: 'log-record/:account/:nickname/:rank', component: LogRecordComponent},
  {path: 'resources-management', component: ResourcesManagementComponent},
  {path: 'role-management/:account/:nickname/:rank', component: RoleManagementComponent},
  {path: 'vip-management', component: VipManagementComponent},
  {path: 'product-menu-management', component: ProductMenuManagementComponent},
  {path: 'product-resource-management', component: ProductResourceManagementComponent},
  {path: 'product-role-management', component: ProductRoleManagementComponent}, // 产品角色管理
  {path: 'product-configuration', component: ProductConfigurationComponent}, // 产品配置
  {path: 'commodity-management', component: CommodityManagementComponent}, // 商品管理
  {path: 'adduser', component: AdduserComponent},
  {path: 'user-modify-password', component: UserModifyPasswordComponent},
  {path: 'addmenu', component: AddmenuComponent},
  {path: 'addresources', component: AddresourcesComponent},
  {path: 'extension-agent', component: ExtensionAgentComponent}, // 推广员管理
  {path: 'deal-query', component: DealQueryComponent},  // 交易明细查询
  {path: 'order-review', component: OrderReviewComponent}, // 订单审核
  {path: 'create-order', component: CreateOrderComponent}, // 创建订单
  {path: 'demographic', component: DemographicComponent}, // 推广员用户统计
  {path: 'trade-statistic', component: TradeStatisticComponent}, // 商品交易统计
  {path: 'extension-add', component: ExtensionAddComponent}, // 推官员增加
  {path: 'commodity-management-add', component: CommodityManagementAddComponent}, // 商品管理添加
  {path: 'commodity-sales-management', component: CommoditySalesManagementComponent}, // 商品管理-》促销管理
  {path: 'sales-management-add', component: SalesManagementAddComponent}, // 促销管理添加
  {path: 'product-configuration-add', component: ProductConfigurationAddComponent}, // 产品配置添加
  {path: 'product-role-management-add', component: ProductRoleManagementAddComponent}, // 产品角色添加
  {path: 'product-role-managemen-binding', component: ProductRoleManagemenBindingComponent}, // 产品角色是绑定
  {path: 'product-resource-management-add', component: ProductResourceManagementAddComponent}, // 产品资源管理添加
  {path: 'product-resource-management-binding', component: ProductResourceManagementBindingComponent}, // 产品资源管理绑定
  {path: 'binding-role', component: BindingRoleComponent},
  {path: 'addrole', component: AddroleComponent},
  {path: 'binding-resources', component: BindingResourcesComponent},
  {path: 'add-vip', component: AddVipComponent},
  {path: 'vip-modify-password', component: VipModifyPasswordComponent},
  {path: 'vip-open', component: VipOpenComponent},
  {path: 'add-product-menu', component: AddProductMenuComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routeConfig)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



